.xlarge {
  font-size: 30px;
}

.large {
  font-size: 25px;
}

.medium {
  font-size: 20px;
}

.lead {
  font-size: 14px;
}

.btn {
  outline: none;
  border: 0;
  background: 0;
}

.btn-bark {
  padding: 13px 30px;
  background-color: #000;
  color: #fff;
  border-radius: 30px;
}

.Contacts {
  max-width: 940px;
  margin: 0 auto;
  text-align: center;
  padding: 114px 0;
}
.Contacts h1 {
  margin-bottom: 20px;
}
.Contacts p {
  margin-bottom: 30px;
}
.Contacts button {
  margin-bottom: 70px;
}
.Contacts nav {
  justify-content: space-between;
}
.Contacts nav a {
  margin: 0 30px;
  color: #070707;
}

header {
  margin: 0 auto;
  padding: 32px 20px;
  max-width: 940px;
  width: 100%;
}
header nav {
  border-bottom: 1.5px solid #828282;
  padding-bottom: 32px;
}
header nav ul {
  display: flex;
  list-style: none;
  justify-content: space-between;
}
header nav ul a {
  transition: all ease-out 0.2s;
  font-size: 20px;
}
header .header-inf {
  padding-top: 32px;
  padding-bottom: 80px;
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  grid-template-areas: "name prof lang" "img img img";
  align-items: center;
  justify-content: space-between;
}
header .header-inf h1 {
  grid-area: name;
  text-align: left;
}
header .header-inf p {
  grid-area: prof;
  text-align: center;
}
header .header-inf button {
  grid-area: lang;
  text-align: right;
  display: flex;
  justify-content: right;
  align-items: center;
}
header .header-inf button .header-btn-inner {
  transform: rotate(90deg);
  width: 60px;
  margin: 0;
}
header .header-inf .header-img {
  grid-area: img;
  margin-top: 32px;
  max-width: 940px;
  width: 100%;
  height: 390px;
  background: #c4c4c4;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  position: relative;
}

a {
  color: #828282;
  text-decoration: none;
}
a:hover {
  color: #070707;
}

.App > section {
  padding-left: 20px;
  padding-right: 20px;
}

.About {
  background: #F6F6F6;
  width: 100%;
}
.About .about-inner {
  padding: 120px 0;
  max-width: 480px;
  margin: 0 auto;
  text-align: center;
  line-height: 34px;
  font-weight: 400;
}

.Skills {
  max-width: 940px;
  margin: 0 auto;
  padding: 100px 0;
  text-align: center;
}
.Skills h1 {
  margin-bottom: 70px;
}
.Skills > p {
  margin-bottom: 70px;
}
.Skills .skills-items {
  display: flex;
  justify-content: space-around;
}
.Skills .skills-items p {
  margin: 20px 0 40px 0;
}
.Skills .skills-items .skills-img {
  width: 90px;
  height: 90px;
  background-color: #c4c4c4;
}

.Portfolio {
  padding: 100px 0 30px 0;
  background-color: #F6F6F6;
}
.Portfolio .portfolio-inner {
  max-width: 940px;
  margin: 0 auto;
  text-align: center;
}
.Portfolio .portfolio-inner h1 {
  padding-bottom: 50px;
}
.Portfolio .portfolio-inner .portfolio-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-bottom: 100px;
}
.Portfolio .portfolio-inner .portfolio-item a {
  text-decoration: underline;
}
.Portfolio .portfolio-inner .portfolio-img {
  max-width: 940px;
  width: 100%;
  height: 530px;
  background-color: #c4c4c4;
  margin-bottom: 40px;
}

@media (width <= 425px) {
  header nav {
    display: none;
  }
  header .header-inf {
    padding-top: 0;
  }

  .skills-items {
    flex-direction: column;
    align-items: center;
  }
  .skills-items .skills-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 50px;
  }
  .skills-items .skills-item .skill-item-stars {
    display: flex;
  }
  .skills-items .skills-item .skill-item-stars i {
    margin: 0 5px;
  }

  .Portfolio .portfolio-img {
    height: 170px !important;
  }
}

