header{
  margin: 0 auto;
  padding: 32px 20px;
  max-width: $max-width;
  width: 100%;

  nav{
    border-bottom: 1.5px solid $secondary-color;
    padding-bottom: 32px;
    ul{
      display: flex;
      list-style: none;
      justify-content: space-between;
      a{
        @include easeOut;
        font-size: $Medium;
      }
    }
  }


  .header-inf{
    padding-top: 32px;
    padding-bottom: 80px;
    display: grid;
    grid-template-columns: 1fr 2fr 1fr;
    grid-template-areas:
    "name prof lang"
    "img img img";

    align-items: center;
    justify-content: space-between;
    
    h1{
      grid-area: name;
      text-align: left;
    }

    p{
      grid-area: prof;
      text-align: center;
    }

    button{
      grid-area: lang;
      text-align: right;
      display: flex;
      justify-content: right;
      align-items: center;

      .header-btn-inner{
        transform: rotate(90deg);
        width: 60px;
        margin: 0;
      }

    }

    .header-img{
      grid-area: img;
      margin-top: 32px;
      max-width: 940px;
      width: 100%;
      height: 390px;
      background: #c4c4c4;
    }
  }
}