@import "./scss/config";
@import "./scss/bundle";
@import "components/header/header";

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body{
  position: relative;
}
a{
  color: $secondary-color;
  text-decoration: none;
  &:hover{
    color: $primary-color;
  }
}

.App >section{
  padding-left: 20px;
  padding-right: 20px;
}

.About{
  background: $bg-color;
  width: 100%;
  .about-inner{
    padding: 120px 0;
    max-width: 480px;
    margin: 0 auto;
    text-align: center;
    line-height: 34px;
    font-weight: 400;
  }
}

.Skills{
  max-width: $max-width;
  margin: 0 auto;
  padding: 100px 0;
  text-align: center;

  h1{
    margin-bottom: 70px;
  }
  &>p{
    margin-bottom: 70px;
  }

  .skills-items{
    display: flex;
    justify-content: space-around;
    p{
      margin: 20px 0 40px 0;
    }

    .skills-img{
      width: 90px;
      height: 90px;
      background-color: #c4c4c4;
    }
  }
}

.Portfolio{
  padding: 100px 0 30px 0;
  background-color: $bg-color;
  .portfolio-inner{
    max-width: $max-width;
    margin: 0 auto;
    text-align: center;

    h1{
      padding-bottom: 50px;
    }

    .portfolio-item {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      padding-bottom: 100px;

      a {
        text-decoration: underline;
      }

    }
    .portfolio-img {
      max-width: 940px;
      width: 100%;
      height: 530px;
      background-color: #c4c4c4;
      margin-bottom: 40px;
    }
  }
}

@media (width <= 425px){

  header{
    nav{
      display: none;
    }
    .header-inf{
      padding-top: 0;
    }
  }

  .skills-items{
    flex-direction: column;
    align-items: center;

    .skills-item{
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-bottom: 50px;

      .skill-item-stars{
        display: flex;
        i{
          margin: 0 5px;
        }
      }
    }
  }
  .Portfolio{
    .portfolio-img{
      height: 170px !important;
    }
  }
}