$primary-color: #070707;
$secondary-color: #828282;
$bg-color: #F6F6F6;

$max-width: 940px;


$XLarge: 30px;
$Large: 25px;
$Medium: 20px;
$Lead: 14px;

.xlarge{
  font-size: $XLarge;
}
.large{
  font-size: $Large;
}
.medium{
  font-size: $Medium;
}
.lead{
  font-size: $Lead;
}

.btn{
  outline: none;
  border: 0;
  background: 0;
}
.btn-bark{
  padding: 13px 30px;
  background-color: #000;
  color: #fff;
  border-radius: 30px;
}


.Contacts{
  max-width: $max-width;
  margin: 0 auto;
  text-align: center;
  padding: 114px 0;

  h1{
    margin-bottom: 20px;
  }

  p{
    margin-bottom: 30px;
  }
  button{
    margin-bottom: 70px;
  }

  nav{
    justify-content: space-between;

    a{
      margin: 0 30px;
      color: $primary-color;
    }
  }
}